import React, { Component } from 'react';
import './css/basket.css';
import minus from './minus.png';
import empty from './delete.png' 

class Basket extends Component {
    render() {
        return (
            <div className="basket">
                <div>
                    {[...new Set(this.props.items)].map(item=>(
                    <p className="row"><img src={minus} onClick={()=>{this.props.deleteItem(this.props.items,item)}}/> {this.props.items.filter(elem => elem === item).length} {item}  </p>
                    ))}
                </div>
                <div className="delete" onClick={this.props.deleteBasket}>
                    <img src={empty} />
                </div>
            </div> 
            
        );
    }
}

export default Basket;
